.App {
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(32, 32, 32)), to(rgb(84, 84, 84)));
  background: -webkit-linear-gradient(rgb(32, 32, 32), rgb(84, 84, 84));
  background: -o-linear-gradient(rgb(32, 32, 32), rgb(84, 84, 84));
  background: linear-gradient(rgb(32, 32, 32), rgb(84, 84, 84));
  height: 100vh;
  padding: 15px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

}
textarea {
  outline: none
}
textarea:focus {
  outline: none
}
button{
  cursor: pointer;
}
button:focus {
  outline: none
}
button:focus {
  outline: none
}
button:active {
  color: black;
  border: 1px solid transparent;

}
button:hover {
  background: transparent;
  color: #38FF9C;
  border: 1px solid #38FF9C;

}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



